body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app__tittle {
  padding: 40px; }

.app__header-container {
  width: 80%;
  margin: auto; }

.app__input {
  padding: 5px;
  width: 100%; }

.app__list {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }

.app__list-item {
  border: 1px solid black;
  margin: 20px auto;
  padding: 20px;
  min-width: 180px; }

.pokemon__name {
  text-transform: capitalize; }

.pokemon__types {
  list-style: none;
  display: inline;
  padding-right: 4px; }

.loader {
  color: aquamarine;
  font-size: 90px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  display: block;
  position: fixed;
  z-index: 1031;
  top: calc( 50% - ( 1em / 2));
  right: calc( 50% - ( 1em / 2));
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  -webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;
          animation: load6 1.7s infinite ease, round 1.7s infinite ease; }

